.block {
  display: block !important;
}

.btn-collapse {
  cursor: pointer;
  .icon-down-2 {
    transform: rotate(180deg);
    transition: all .3s;
  }
}
.btn-collapse.collapsed {
  .icon-down-2 {
    transform: rotate(0deg);
  }
}

.hr-offsetted {
  margin-left: -15px;
  margin-right: -15px;
  height: 0;
  border-bottom: 1px solid $gray;
}
